.addFilm{
  position: fixed;
  z-index: 500;
  background-color: rgb(163, 162, 162);
  width: 70%;
  height: 400px;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px black;
  padding: 16px;
  left: 15%;
  top: 10%;
  box-sizing: border-box;
  transition: all 0.3s ease-out;
 }
.poszlo{
  width: 100%;
  padding: 10px;
  margin-top: 20px;
  margin-bottom: 60px;
}
.zamknij{
position: absolute;
padding: 10px;
bottom: 20px;
right: 20px;
}