body {
  margin: 0;
  padding: 0;
}


.film{
  background-color: #4a4e57;
  padding: 5px;
  border-bottom: 2px solid rgb(141, 119, 119)
}
p{
  margin: 0;
  padding: 1px;
  letter-spacing: 0.3px;
  color: rgb(218, 218, 218);
  font-size: 15px;
  font-weight: 100;
  font-family: arial
}
.input{
  outline: none;
  border: 1px solid #ccc;
  background-color: white;
  font: inherit;
  padding: 10px 10px;
  display: block;
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 10px;
}
.addFilm{
  position: fixed;
  z-index: 500;
  background-color: rgb(163, 162, 162);
  width: 70%;
  height: 400px;
  border: 1px solid #ccc;
  box-shadow: 1px 1px 1px black;
  padding: 16px;
  left: 15%;
  top: 10%;
  box-sizing: border-box;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
 }
.poszlo{
  width: 100%;
  padding: 10px;
  margin-top: 20px;
  margin-bottom: 60px;
}
.zamknij{
position: absolute;
padding: 10px;
bottom: 20px;
right: 20px;
}
.App {
  padding: 10px 0;
  background-color: #282c34;
  text-align: center;
  color: whitesmoke;
  position: relative;
}
.left{
  position: absolute;
  left: 15px;
  top: 6px;
  width: 80px;
  border: none;
  background-color: rgb(172, 160, 162);
  padding: 5px;
  color: rgb(240, 240, 240);
  outline: none;
  float: left;
}

.right{
  position: absolute;
  right: 15px;
  top: 6px;
  width: 80px;
  border: none;
  background-color: rgb(172, 160, 162);
  padding: 5px;
  color: rgb(240, 240, 240);
  outline: none;
  float: left;
}

