.App {
  padding: 10px 0;
  background-color: #282c34;
  text-align: center;
  color: whitesmoke;
  position: relative;
}
.left{
  position: absolute;
  left: 15px;
  top: 6px;
  width: 80px;
  border: none;
  background-color: rgb(172, 160, 162);
  padding: 5px;
  color: rgb(240, 240, 240);
  outline: none;
  float: left;
}

.right{
  position: absolute;
  right: 15px;
  top: 6px;
  width: 80px;
  border: none;
  background-color: rgb(172, 160, 162);
  padding: 5px;
  color: rgb(240, 240, 240);
  outline: none;
  float: left;
}
